import React, {useState} from 'react';
import {Modal, ModalHeader, ModalBody, ModalFooter, FormGroup, Button, Input} from 'reactstrap';
import styled from 'styled-components';

const FooterWrapper = styled.div`
  background-color: #373535;
  padding: 25px 0;
  text-align: center;
  display:flex;
  flex-direction: column;
`;

const FooterContent = styled.span`
  color: #fff;
  opacity: .7;
  margin-bottom: 10px;
  &:last-child{
    margin-bottom: 0;
  }
`;

const Btn = styled.button`
  padding: 0;
  background-color: transparent;
  color: #fff;
  box-shadow: 0 0 0 0 transparent;
  border: none;
  transition: color 0.3s;
  &:hover{
    color: var(--primary);
    text-decoration: underline;
  }
  &:focus,
  &:active{
    outline: 0;
    box-shadow: 0 0 0 0 transparent;
  }
`;

export const Footer = () => {
    const [message, setMessage] = useState('Pacman pizza: bug report. ');

    const [modal, setModal] = useState(false);

    const clearMessage = () => setMessage('Pacman pizza: bug report. \n');

    const toggle = () => {
        clearMessage();
        setModal(!modal)
    };

    const submit = () => {
        fetch(`https://api.telegram.org/bot707714396:AAH4tJZYIXJWWe9SEkvkiuSrIq8KShR5cLk/sendMessage?chat_id=-136942741&parse_mode=html&text=${message}`)
            .then(res => res.json())
            .then(res => {
                toggle();
                console.log(res)
            })
    };

    const year = new Date().getFullYear();

    return (
        <FooterWrapper>
            <FooterContent>Pacman Pizza © {year}</FooterContent>
            <FooterContent>
                <Btn onClick={toggle}>Report a bug</Btn>
            </FooterContent>
            <Modal isOpen={modal} toggle={toggle}>
                <ModalHeader toggle={toggle}>Report a bug</ModalHeader>
                <ModalBody>
                    <FormGroup>
                        <Input
                            type="textarea"
                            name="message"
                            id="message"
                            value={message}
                            onChange={e => setMessage(e.target.value)}
                        />
                    </FormGroup>
                </ModalBody>
                <ModalFooter>
                    <Button outline color="secondary" onClick={toggle}>Cancel</Button>
                    <Button color="primary" onClick={submit}>Send</Button>
                </ModalFooter>
            </Modal>
        </FooterWrapper>
    );
};
